import { Component, OnInit } from '@angular/core';
import {DataService} from '../../services/data.service';
declare var Morris: any;

@Component({
  selector: 'app-line-chart',
  templateUrl: './line-chart.component.html',
  styleUrls: ['./line-chart.component.css']
})
export class LineChartComponent implements OnInit {
  public device_id = "592ce06a49091926c34bfd74";
  public rows: any;
  public showGraph = false;

    public chart=[];
    public chartData2 = [
          {x: '2013-03-30 22:00:00', y: 3, z: 3},
          {x: '2013-03-31 00:00:00', y: 2, z: 0},
          {x: '2013-03-31 02:00:00', y: 0, z: 2},
          {x: '2013-03-31 04:00:00', y: 4, z: 4}
        ];
    public chartElement=['myfirstchart1', 'myfirstchart2', 'myfirstchart3', 'myfirstchart4'];
    public labels = ['t', 's', 'h', 'l'];
    public stiker_label = [
        {name: "Temperature", label: 't'}, 
        {name: "Soil Humidity", label: 's'}, 
        {name: "LED Status", label: 'led'},
        {name: "Water", label: 'water'}];
    public stikers = [];

    public chartData = [];

  constructor(private dataService: DataService) { }

  ngOnInit() {
    this.dataService.getData(this.device_id).subscribe(data=>{
      this.rows = data.data;
      this.getData1();
      //console.log(this.rows);
      for (let i=0; i<4; i++){
      this.chart[i] = new Morris.Line({
        // ID of the element in which to draw the chart.
        element: this.chartElement[i],
        // Chart data records -- each entry in this array corresponds to a point on
        // the chart.
        data: this.getData(),
        xkey: 'd',
        ykeys: this.labels[i],
        labels: ['Date', 'Value']
      });
      if(this.chartData!=[])
        this.showGraph = true; 
      /*this.chart[i] = new Morris.Line({
        // ID of the element in which to draw the chart.
        element: this.chartElement[i],
        // Chart data records -- each entry in this array corresponds to a point on
        // the chart.
        data: this.getData1(),
        xkey: 'x',
        ykeys: 'y',
        labels: ['Date', 'Value']
      });*/
      //this.chartData2[i].y;
      }
      this.stikers[0]={name: this.stiker_label[0].name, 
                      value: this.chartData[this.chartData.length-1].t};
      this.stikers[1]={name: this.stiker_label[1].name, 
                      value: this.chartData[this.chartData.length-1].s};
      this.stikers[2]={name: this.stiker_label[2].name, 
                      value: this.chartData[this.chartData.length-1].led};
      this.stikers[3]={name: this.stiker_label[3].name, 
                      value: this.chartData[this.chartData.length-1].water};
      console.log(this.stikers);
    });
  }  

  onClickButton(){
    //let array = this.getData();
    /*var str = this.chartData[0];
    console.log(this.chartData[0]);
    this.chartData2.push({x: str.date.toString(), y: Number(str.temp), z: 4}); //x: str.date, y: str.temp, z: str.hum
    for(let i=0; i<4; i++)
      this.chart[i].setData(this.chartData2);
    console.log(this.chartData2);*/
    this.loadData();   
  } 

  getData(){    
   for (let row of this.rows){
      let date = this.formatDate(row.date).toString();
      let data = row.data;
      let temp = Number(data[0].value);
      let hum = Number(data[1].value);
      let soil = Number(data[2].value);
      let light = Number(data[3].value);
      let led = data[4].value.toString();
      let water = data[5].value.toString();
      this.chartData.push({
        d: date.toString(), 
        t: Number(temp),
        h: Number(hum),
        s: Number(soil),
        l: Number(light),
        led: led.toString(),
        water: water.toString()
      });
    }
    console.log(this.chartData);
    return this.chartData;
    //console.log(this.chartData);
    //return this.chartData2;
  }

  getData1(){
    console.log(this.chartData2);
    return this.chartData2;
  }

  loadData(){
    this.dataService.getData(this.device_id).subscribe(data=>{
      this.rows = data.data;
      this.getData();
      for(let i=0; i<4; i++)
        this.chart[i].setData(this.chartData);
      console.log(this.chartData);
    });
  }

  formatDate(mongodb_date){
    var d = new Date(mongodb_date);
    var date = d.getFullYear()+ "-" + ("0"+(d.getMonth()+1)).slice(-2) + "-" 
    + ("0" + d.getDate()).slice(-2) + " "+ ("0" + d.getHours()).slice(-2) 
    + ":" + ("0" + d.getMinutes()).slice(-2) + ":" + ("0" + d.getSeconds()).slice(-2);
    /*var datestring = ("0" + d.getDate()).slice(-2) + "-" + ("0"+(d.getMonth()+1)).slice(-2) + "-" +
    d.getFullYear() + " " + ("0" + d.getHours()).slice(-2) + ":" + ("0" + d.getMinutes()).slice(-2);*/
    return date;
    //return new Date(mongodb_date);
  }


}


