import { Component, OnInit } from '@angular/core';
import {AuthService} from '../../services/auth.service';
import {ConfigService} from '../../services/config.service';
import {FlashMessagesService} from 'angular2-flash-messages';
import {Router} from '@angular/router';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  user:any;
  projects: any;
  devices: any;
  sensors: any;
  actus: any;
  new_title: String;
  new_name: String;
  new_sens: String;
  sens_value: String;
  sens_prefix: String;
  new_actu: String;
  actu_states: String;
  currentProject_selection = 0;
  currentDevice_selection = 0;
  newProject = false;
  newDevice = false;
  newSensor = false;
  newActuator = false;
  collapsed = [false,false,false];
  collapsed2 = [false,false,false];


  constructor(private authService: AuthService,
          private configService: ConfigService,
          private flashMessage: FlashMessagesService,
  				private router: Router) { }

  ngOnInit() {
  	this.authService.getProfile().subscribe(profile => {
  		this.user = profile.user;
      this.configService.getProjects(this.user).subscribe(data=>{
        this.projects = data.projects;
        this.collapsed[0] = false;
        console.log(this.projects);
        this.findAllDevices();
      });
  	}, err => {
  		console.log(err);
  		return false;
  	});
  }

  onClickToggled(param){ // click handler
      let bool = this.collapsed[param];
      if(this.currentProject_selection!=0)
        this.collapsed[this.currentProject_selection-1] = false;
      if(!bool){
        this.showDevices(param);
        this.currentProject_selection = param+1;
      }else{
        this.currentProject_selection = 0;
      }
      this.collapsed[param] = bool === false ? true : false;
      return false;
  }

  onClickToggled2(param){ // click handler
      let bool = this.collapsed2[param];
      if(this.currentDevice_selection!=0)
        this.collapsed2[this.currentDevice_selection-1] = false;
      if(!bool){
        this.showSensors(param, this.currentProject_selection-1);
        this.currentDevice_selection = param+1;
      }else{
        this.currentDevice_selection = 0;
      }
      this.collapsed2[param] = bool === false ? true : false;
      return false;
  }

  onClickAddProject(){
    this.newProject = !this.newProject;
    return false;
  };
  onClickAddDevice(){
    this.newDevice = !this.newDevice;
    return false;
  };
  onClickAddSensor(){
    this.newSensor = !this.newSensor;
    return false;
  };
  onClickAddActuator(){
    this.newActuator = !this.newActuator;
    return false;
  };

  onClickCreateProject(){
    let user = this.user;
    const project = {
      title: this.new_title,
      owner: user._id
    }
    this.configService.addProject(project).subscribe(data =>{
      if(data.success){
        this.flashMessage.show(data.msg, {
          cssClass: 'alert-success', 
          timeout: 5000
        });
        this.newProject = !this.newProject;
        this.configService.getProjects(this.user).subscribe(data=>{
          this.projects = data.projects;
          console.log(this.projects);
        });
      }else{
        this.flashMessage.show(data.msg, {
          cssClass: 'alert-danger', 
          timeout: 5000
        });
      }
    });
    return false;
  };

  onClickCreateDevice(project_num){
    const project_id = this.projects[project_num]._id;
    const device = {
      name: this.new_name,
      project: project_id
    }
    this.configService.addDevice(device).subscribe(data =>{
      if(data.success){
        this.flashMessage.show(data.msg, {
          cssClass: 'alert-success', 
          timeout: 5000
        });
        this.newDevice = !this.newDevice;
        this.findDevicesByProjectId(project_id, project_num);
      }else{
        this.flashMessage.show(data.msg, {
          cssClass: 'alert-danger', 
          timeout: 5000
        });
      }
    });
    return false;
  };

  onClickCreateSensor(device_num){
    const device_id = this.projects[this.currentProject_selection-1].devices[device_num]._id;
    const sensor = {
      device_id: device_id,
      name: this.new_sens,
      value: this.sens_value,
      prefix: this.sens_prefix
    }
    this.configService.addSensor(sensor).subscribe(data =>{
      if(data.success){
        this.flashMessage.show(data.msg, {
          cssClass: 'alert-success', 
          timeout: 5000
        });
        this.newSensor = !this.newSensor;
        this.findDevicesByProjectId(
            this.projects[this.currentProject_selection-1]._id,
            this.currentProject_selection-1
        );
      }else{
        this.flashMessage.show(data.msg, {
          cssClass: 'alert-danger', 
          timeout: 5000
        });
      }
    });
    return false;
  };

  onClickCreateActuator(device_num){
    const device_id = this.projects[this.currentProject_selection-1].devices[device_num]._id;
    const actuator = {
      device_id: device_id,
      name: this.new_actu,
      states: this.actu_states
    }
    this.configService.addActuator(actuator).subscribe(data =>{
      if(data.success){
        this.flashMessage.show(data.msg, {
          cssClass: 'alert-success', 
          timeout: 5000
        });
        this.newActuator = !this.newActuator;
        this.findDevicesByProjectId(
            this.projects[this.currentProject_selection-1]._id,
            this.currentProject_selection-1
        );
        this.showSensors(device_num, this.currentProject_selection-1);
      }else{
        this.flashMessage.show(data.msg, {
          cssClass: 'alert-danger', 
          timeout: 5000
        });
      }
    });
    return false;
  };

  findDevicesByProjectId(project_id, project_num){
    
    this.configService.devicesList(project_id).subscribe(data =>{
      //console.log("dev"+data.devices);
      this.projects[project_num].devices = data.devices;
      this.devices = this.projects[project_num].devices;
      console.log(this.devices);

      this.showSensors(this.currentDevice_selection-1, this.currentProject_selection-1);
    });
    return false;
  };

  findAllDevices(){
    /*let project = this.projects[0];
    this.configService.devicesList(project._id).subscribe(data=>{
      this.devices = data.devices;
      this.collapsed[0] = false;
      console.log(this.devices);
    });*/
    let i = 0;
    for (let project of this.projects){
      let project_id = project._id;
      this.configService.devicesList(project_id).subscribe(data =>{
      this.projects[i].devices = data.devices;
      this.devices = this.projects[i].devices;
      console.log(this.devices);
      i++;
    });
    }
    return false;
  };  

  showDevices(project_num){
    this.devices = this.projects[project_num].devices;
    //console.log(this.devices[0]);
  }

  showSensors(device_num, project_num){
    this.sensors = this.projects[project_num].devices[device_num].sensors;
    this.actus = this.projects[project_num].devices[device_num].actuators;
    //console.log(this.devices[0]);
  }

}



